import React, { useCallback, useEffect, useState } from "react";
import { useDropzone } from "react-dropzone";
import { sha256 } from "js-sha256";
import { Button, message, Popconfirm, Space, Table, Tag } from "antd";
import { MedicineBoxTwoTone } from "@ant-design/icons";
import { useSelector } from "react-redux";
import "../../assets/File.css";
export const getReverseFileExtension = (type) => {
    switch (Object.keys(type)[0]) {
        case "jpeg":
            return "image/jpeg";
        case "gif":
            return "image/gif";
        case "jpg":
            return "image/jpg";
        case "png":
            return "image/png";
        case "svg":
            return "image/svg";
        case "avi":
            return "video/avi";
        case "mp4":
            return "video/mp4";
        case "aac":
            return "video/aac";
        case "wav":
            return "audio/wav";
        case "mp3":
            return "audio/mp3";
        case "wasm":
            return "application/wasm";
        default:
            return "";
    }
};
const getFileExtension = (type) => {
    switch (type) {
        case "image/jpeg":
            return { jpeg: null };
        case "image/gif":
            return { gif: null };
        case "image/jpg":
            return { jpg: null };
        case "image/png":
            return { png: null };
        case "image/svg":
            return { svg: null };
        case "video/avi":
            return { avi: null };
        case "video/aac":
            return { aac: null };
        case "video/mp4":
            return { mp4: null };
        case "audio/wav":
            return { wav: null };
        case "audio/mp3":
            return { mp3: null };
        case "audio/mpeg":
            return { mp3: null };
        case "application/wasm":
            return { wasm: null };
        default:
            return null;
    }
};
let fileKeyBuffer = "";
let fileTypeBuffer = "";
let bucketId = "";
let filename = "";
let newData = [];
let arrString = "";
let flag = true;
let selectedRows = [];
const deletePromise = [];
function allFile() {
    const [key, setkey] = useState("");
    const { principal, assets, bucketActor } = useSelector((state) => state);
    const storageKey = principal.value;
    let localStor = JSON.parse(localStorage.getItem(storageKey));
    const localBuffer = localStor === null ? [] : localStor;
    const assetExts = assets === null ? [] : assets.ok;
    if (localBuffer.length < assetExts.length && flag) {
        for (let i = 0; i < assetExts.length; i++) {
            localBuffer[i] = {
                filekey: assetExts[i].file_key,
                bucketId: assetExts[i].bucket_id.toString(),
                filename: assetExts[i].file_name,
                filetype: [Object.keys(assetExts[i].file_extension)[0]],
                id: assetExts[i].file_key,
            };
        }
    }
    function allWithProgress(requests) {
        requests.forEach((item) => {
            item.then(() => {
                location.reload();
            });
        });
        return Promise.all(requests);
    }
    const onDrop = useCallback((acceptedFiles) => {
        // Do something with the files
        console.log(acceptedFiles);
        let total_size = 0;
        for (let i = 0; i < acceptedFiles.length; i++) {
            const file = acceptedFiles[i];
            let fileKey = "";
            // filename = file.name;
            const digestTree = [];
            const dataArr = [];
            const tmpArr = [];
            let fileSize = file.size; // 文件大小
            total_size += fileSize;
            console.log("fileSize:", fileSize);
            let chunkSize = 1992295; // 切片的大小
            let chunks = Math.ceil(fileSize / chunkSize); // 获取切片的个数
            console.log("chunks:", chunks);
            let blobSlice = //
             File.prototype.slice ||
                File.prototype.mozSlice ||
                File.prototype.webkitSlice;
            let currentChunk = 0;
            const chunkPromises = [];
            const reader = new FileReader();
            let file_extension = getFileExtension(file.type);
            // fileType = Object.keys(file_extension)[0];
            reader.onload = function (e) {
                console.log("333");
                let result = e.target.result;
                let tData = new Uint8Array(result);
                let data = [];
                for (let i = 0; i < tData.length; i++) {
                    data.push(tData[i]);
                }
                let tmp = sha256.digest(data);
                tmpArr.push(tmp);
                dataArr.push(data);
                digestTree.push(...tmp);
                currentChunk++;
                if (currentChunk < chunks) {
                    loadNext();
                }
                else {
                    // let root = sha256.digest(digestTree);
                    fileKey = sha256(digestTree);
                    // digestTree.splice(0, 0, ...root);
                    fileKey = fileKey.toUpperCase();
                    let isRepeat = localBuffer.find((item) => item.filekey === fileKey);
                    if (isRepeat) {
                        message.error(`${fileKey} is Repeat`);
                    }
                    else {
                        console.log("ok");
                        for (let i = 0; i < dataArr.length; i++) {
                            console.log(fileKey);
                            chunkPromises.push(bucketActor.put({
                                file_key: fileKey,
                                file_name: file.name,
                                file_extension: file_extension,
                                chunk: { digest: tmpArr[i], data: dataArr[i] },
                                chunk_number: chunks,
                                order: i,
                            }));
                        }
                        console.log("111111");
                        allWithProgress(chunkPromises).then((re3) => {
                            console.log(re3);
                            for (let i = 0; i < re3.length; i++) {
                                if (re3[i].ok.upload_status === true) {
                                    console.log(re3[i]);
                                    fileKeyBuffer = re3[i].ok.file_key;
                                    filename = re3[i].ok.file_name;
                                    fileTypeBuffer = Object.keys(re3[i].ok.file_extension)[0];
                                    bucketId = re3[i].ok.bucket_id.toString();
                                    setkey({});
                                    break;
                                }
                            }
                        });
                    }
                }
            };
            const loadNext = () => {
                console.log("222222");
                let start = currentChunk * chunkSize;
                let end = start + chunkSize > fileSize ? fileSize : start + chunkSize;
                reader.readAsArrayBuffer(blobSlice.call(file, start, end));
            };
            loadNext();
        }
    }, [localBuffer, bucketActor]);
    useEffect(() => {
        return () => {
            Promise.all(deletePromise).then();
        };
    });
    const loadImg = async (key) => {
        console.log(key);
        let re = await bucketActor.getAssetExtkey(key);
        console.log("RE", re);
        let chunks = Number(re.ok.need_query_times);
        console.log(chunks);
        let file = [];
        for (let i = 0; i < chunks; i++) {
            let reFile = await bucketActor.get({
                file_key: key,
                flag: i,
            });
            console.log(i);
            console.log(reFile);
            let tmp = reFile.ok[0];
            for (let i = 1; i < reFile.ok.length; i++)
                tmp = tmp.concat(reFile.ok[i]);
            file = file.concat(tmp);
        }
        console.log(file);
        let ff = new Uint8Array(file).buffer;
        console.log(ff);
        let file_type = getReverseFileExtension(re.ok.file_extension);
        const blob = new Blob([ff], {
            type: file_type,
        });
        console.log(blob);
        // console.log(sha256(blob).words);
        return URL.createObjectURL(blob);
    };
    const skip = (key) => {
        return async () => {
            loadImg(key).then((url) => {
                if (url) {
                    window.open(`${url}`);
                }
            });
        };
    };
    const confirm = async () => {
        console.log(selectedRows);
        if (selectedRows.length > 0) {
            const newBuffer = JSON.parse(localStorage.getItem(storageKey));
            for (let i = 0; i < selectedRows.length; i++) {
                deletePromise.push(bucketActor.deletekey(selectedRows[i].filekey));
                let index = newBuffer.findIndex((item) => {
                    return selectedRows[i].filekey === item.filekey;
                });
                if (index !== -1) {
                    newBuffer.splice(index, 1);
                }
                else {
                    message.error("Please select the file to delete!");
                    return 0;
                }
            }
            console.log(newBuffer);
            flag = newBuffer.length === assetExts.length;
            const newStorage = JSON.stringify(newBuffer);
            if (storageKey !== "")
                localStorage.setItem(storageKey, newStorage);
            setkey({});
            message.success("Deleted successded");
        }
        else {
            message.error("Please select the file to delete!");
        }
    };
    const cancel = () => {
        message.error("Cancel deletion");
    };
    const onSelectChange = (_, Rows) => {
        selectedRows = Rows;
    };
    const rowSelection = {
        onChange: onSelectChange,
    };
    const columns = [
        {
            title: "FileName",
            dataIndex: "filename",
        },
        {
            title: "BucketId",
            dataIndex: "bucketId",
        },
        {
            title: "FileKey",
            dataIndex: "filekey",
        },
        {
            title: "FileType",
            dataIndex: "filetype",
            render: (tags) => (React.createElement("span", null, tags.map((tag) => {
                let color;
                switch (tag) {
                    case "mp3":
                    case "wav":
                        color = "volcano";
                        break;
                    case "acc":
                    case "mp4":
                    case "avi":
                        color = "geekblue";
                        break;
                    default:
                        color = "magenta";
                        break;
                }
                return (React.createElement(Tag, { color: color, key: Date.now() }, tag));
            }))),
        },
        {
            title: "Action",
            key: "action",
            render: (_, record) => (React.createElement(Space, { size: "middle" },
                React.createElement(Button, { type: "primary", onClick: skip(record.filekey) }, "Entry"))),
        },
    ];
    const data = [];
    if (fileKeyBuffer !== "") {
        data.push({
            id: fileKeyBuffer,
            filename: filename,
            bucketId: bucketId,
            filekey: fileKeyBuffer,
            filetype: [fileTypeBuffer],
        });
    }
    fileKeyBuffer = "";
    // localStorage.removeItem("ic-identity");
    // localStorage.removeItem("ic-delegation");
    // localStorage.removeItem(
    //   "eniwd-yttfc-vdnj4-4rx5q-6tnr5-g2hxx-2dfcx-6pbcc-xbrg2-oivre-4ae"
    // );
    if (localBuffer.length > 0) {
        newData = [...localBuffer, ...data];
    }
    else {
        newData = data;
    }
    arrString = JSON.stringify(newData);
    if (newData.length > 0 && storageKey !== "")
        localStorage.setItem(storageKey, arrString);
    const { getRootProps, getInputProps } = useDropzone({ onDrop });
    return (React.createElement("div", { className: "showTable" },
        React.createElement("div", { className: "title" },
            React.createElement("div", null,
                React.createElement("div", { className: "drop", ...getRootProps() },
                    React.createElement("input", { ...getInputProps() }),
                    React.createElement("div", { className: "uploadBac" },
                        React.createElement("p", null,
                            React.createElement(MedicineBoxTwoTone, { className: "icon" })),
                        React.createElement("p", null, "Click one or drag many files to this area to upload"))))),
        React.createElement(Table, { className: "fileTable", rowSelection: rowSelection, rowKey: (record) => record.id, columns: columns, pagination: { position: "bottomRight" }, dataSource: newData }),
        React.createElement("div", { className: "action_bac" },
            React.createElement(Space, { style: { display: newData.length > 0 ? "inline-flex" : "none" }, size: "middle", className: "Action" },
                React.createElement(Popconfirm, { title: "Are you sure to delete these files?", onConfirm: confirm, onCancel: cancel, okText: "Yes", cancelText: "No" },
                    React.createElement(Button, { type: "primary" }, "Delete"))))));
}
export default allFile;
